<template>
	<div id="GPS">
    <div class="header">
      <div class="van-hairline--bottom van-nav-bar">
        <div class="van-nav-bar__left">
          <i class="van-icon van-icon-arrow van-nav-bar__arrow" @click="$router.back(-1)" style="color: #000000 !important;"></i>
        </div>
        <div class="van-ellipsis van-nav-bar__title">定位</div>
      </div>
    </div>
    <div class="login_box">
      <div class="login_bar background_color_ffffff">
        <img src="static/img/icon_dw_dw11111.png" class="img_top_center width_5"/>
        <span class="font_14 color_8b8b8b" style="margin-left: 10px">江苏省徐州市泉山区</span>
      </div>
      <div class="login_bar background_color_ffffff login_top font_14">
        <span>位置选择</span>
        <span style="margin-left: 10px" class="color_8b8b8b" @click="show=!show">{{cityName}}</span>
      </div>
    </div>
    <van-actionsheet
      v-model="show"
      :actions="actions"
      @select="onSelect"
    />
	</div>
</template>
<script>
	export default {
		name: 'login',
		data() {
			return {
        cityName:'请选择城市',
        show:false,
        actions: [],
			}
		},
    mounted() {
      this.getCityList();
    },
		methods:{
      getCityList(){
        let $this = this;
        let obj = {
          userid: $this.userId,
        };
        let qs = require('qs');
        $this.$ajax.post("city/getcitys", qs.stringify(obj), {
          headers: {
            'Content-Type': 'application/x-www-form-urlencoded'
          }
        })
        .then(function(rpdata) {
          if(rpdata.data.meta.code=='200'){
            for(let i = 0;i<rpdata.data.data.length;i++){
              rpdata.data.data[i].name = rpdata.data.data[i].cityname;
              rpdata.data.data[i].id = rpdata.data.data[i].cityid;
            }
            $this.actions = rpdata.data.data;
          }
        })
        .catch(function(err) {})
      },
      onSelect(item) {
        this.show = false;
        this.cityName = item.cityname;
        this.cityId = item.cityid;
        localStorage.setItem("cityName",item.cityname);
        localStorage.setItem("cityId",item.cityid);
      }
		}
	}
</script>
<style scoped>
  .width_5{
    width: 5%;
  }
  .login_box{
    margin-top: 44px;
  }
  .login_top{
    border-top: 5px solid #eeeeee;
  }
  .login_bar{
    padding: 15px 15px;
  }
</style>



